<template>
  <div class="container" @click="closeColor" :class="{closed}">
    <!-- <div class="layerImg">
      <a target="blank" v-if="hover" :href="'https://www.reddit.com/r/Layer/comments/' + item.url">
        <img :src="layerUrl" />
      </a>
    </div>-->
    <!-- <div>
      <label for="id">
        Layer id
        <span @click="random" class="dice">ðŸŽ²</span>
      </label>
      <input type="number" name="id" :value="$store.state.id" @change="updateId" />
    </div>-->
    <div>
      <label for="width">Width</label>
      <input type="number" name="width" v-model.lazy="$store.state.width" />
    </div>
    <div>
      <label for="height">Height</label>
      <input type="number" name="height" v-model.lazy="$store.state.height" />
    </div>
    <div>
      <label for="background">Background</label>
      <!-- <input type="color" name="background" class="color" v-model="$store.state.background" /> -->
      <div class="colorField" :style="{ background: $store.state.background}" @click="toggleClick" />
    </div>
    <div class="colorBox" v-if="openColor" @click="cancelClick">
      <chrome :value="$store.state.background" @input="updateBackground" :disableFields="true" />
    </div>
    <div>
      <label for="size">{{ tooltipNum ? tooltipNum + ' Layers' : 'How many' }}</label>
      <input
        type="range"
        min="1"
        max="100"
        step="0.1"
        name="size"
        @input="tooltipNum = numScale($event.target.value)"
        @change="tooltipNum = null, $store.state.size = numScale($event.target.value)"
        :value="numScale.invert(tooltipNum ? tooltipNum : $store.state.size)"
      />
    </div>
    <div>
      <label for="size">{{ tooltipSize }}</label>
      <input
        type="range"
        min="0.1"
        max="1"
        step="0.01"
        name="scale"
        @input="tooltipSize = $event.target.value"
        @change="tooltipSize = 'Size'"
        v-model="$store.state.scale"
      />
    </div>
    <div class="credits">
      Images by redditors of
      <a href="https://reddit.com/r/layer" target="_blank">/r/layer</a>,
      Tooling by
      <a href="https://twitter.com/chrispiecom" target="_blank">chrispie</a>
    </div>
    <div class="menu">
      <div class="hamburger" :class="{arrow: !closed}" @click="closed = !closed">
        <div class="a"></div>
        <div class="b"></div>
        <div class="c"></div>
      </div>
      <svg class="saveIcon" viewBox="0 0 24 24" @click="download">
        <path d="M8 6h-5v15h18v-15h-5v-3h8v21h-24v-21h8v3zm5 6h4l-5 6-5-6h4v-12h2v12z" />
      </svg>
      <router-link to="/">
        <svg
          class="searchIcon"
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
        >
          <path
            d="M23.809 21.646l-6.205-6.205c1.167-1.605 1.857-3.579 1.857-5.711 0-5.365-4.365-9.73-9.731-9.73-5.365 0-9.73 4.365-9.73 9.73 0 5.366 4.365 9.73 9.73 9.73 2.034 0 3.923-.627 5.487-1.698l6.238 6.238 2.354-2.354zm-20.955-11.916c0-3.792 3.085-6.877 6.877-6.877s6.877 3.085 6.877 6.877-3.085 6.877-6.877 6.877c-3.793 0-6.877-3.085-6.877-6.877z"
          />
        </svg>
      </router-link>
    </div>

    <!-- <button type="submit" @click="submit" class="button">Generate</button> -->
    <!-- <button class="button lucky" @click="random">?!</button> -->
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { global, makeUrl } from "../utils.js";
import { Chrome } from "vue-color";
import { saveAs } from "file-saver";
import { scalePow } from "d3-scale";

export default {
  name: "settings",
  data: function() {
    return {
      dataUrl: "",
      openColor: false,
      tooltipSize: "Size",
      tooltipNum: null,
      closed: true
    };
  },
  components: { Chrome },
  methods: {
    cancelClick: function(e) {
      e.stopPropagation();
      this.openColor = true;
    },
    toggleClick: function(e) {
      e.stopPropagation();
      this.openColor = !this.openColor;
    },
    inputSize: function(e) {
      console.log(e.target.value);
    },
    closeColor: function(e) {
      if (this.openColor) {
        this.openColor = false;
      }
    },
    download: async function(e) {
      const blob = await canvas2png(global.canvas);
      saveAs(blob, "layer.png");
      // const url = URL.createObjectURL(blob);
      // this.dataUrl = url;
      // const data = global.canvas.toDataURL("image/png");
      // this.dataUrl = data;
      // console.log(data.length);
      // await new Promise(requestAnimationFrame);
      // URL.revokeObjectURL(url);
      // this.dataUrl = "";
      // const url = global.canvas
      //   .toDataURL("image/png")
      //   .replace("image/png", "image/octet-stream");
      // console.log(url);
    },
    updateId: function(e) {
      // console.log(e.target.value);
      // this.$store.dispatch("setId", e.target.value);
      const id = e.target.value;
      this.$router.push("/" + id);
    },
    updateBackground: function(color) {
      // console.log(color.hex);
      this.$store.state.background = color.hex;
    },
    submit: function(e) {
      console.log("submit", this.id);
      e.preventDefault();
      //this.$router.push("/" + this.id);
    },
    random: function(e) {
      // console.log("random");
      const id = this.data[parseInt(Math.random() * this.data.length)].layerId;
      // this.$store.dispatch("setId", id);
      this.$router.push("/" + id);
      e.preventDefault();
    }
  },
  computed: {
    ...mapGetters(["data", "siblingsFiltered"]),
    ...mapState(["loaded", "size", "hover"]),
    item: function() {
      return this.siblingsFiltered.find(d => d.layerId === this.hover);
    },
    layerUrl: function() {
      return makeUrl(this.item.id);
    },
    numScale: function() {
      return scalePow()
        .exponent(2)
        .domain([1, 100])
        .rangeRound([10, 3000])
        .nice();
    }
  }
};

async function canvas2png(canvas) {
  return new Promise(resolve => canvas.toBlob(resolve));
}
</script>

<style scoped lang="stylus">
.container {
  padding: 20px;
  width: 270px;
  font-size: 14px;
  position: absolute;
  background: #ffffffeb;
  right: 0;
  display: flex;
  flex-flow: column;
  user-select: none;
  backdrop-filter: blur(2px);
  font-family: Helvetica, Arial, sans-serif;
  transition: transform 0.6s;

  &.closed {
    transform: translate(0px, calc(-100% + 55px));
  }

  > div {
    padding-bottom: 1.2em;
    clear: both;
  }
}

.layerImg {
  justify-content: center;
  display: flex;
}

label {
  width: 100px;
  display: inline-block;
  float: left;
  height: 1em;
  padding-bottom: 5px;
  padding-top: 5px;
}

input {
  font-size: 15px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  height: 30px;
  width: 120px;
  text-align: center;
  float: left;
}

.colorField {
  width: 120px;
  height: 30px;
  display: inline-block;
  float: left;
  border-radius: 3px;
  border: 1px solid #ccc;
}

.colorBox {
  margin: auto;
}

.dice {
  cursor: pointer;
}

.credits {
  // font-size: 12px;
  line-height: 1.4em;
  color: #555;
}

.download {
  font-size: 15px;
  padding: 10px;
  border-radius: 3px;
  border: none;
  color: #000;
  background: #fff;
  cursor: pointer;
  line-height: 1em;
  width: 100%;

  &:hover {
    background: #ffffff7d;
  }
}

input[type='color']::-webkit-color-swatch-wrapper {
  padding: 0;
}

input[type='color']::-webkit-color-swatch {
  border: none;
}

input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input[type='range'] {
  -webkit-appearance: none;
  background: none;
  border: none;
}

input[type='range']::-webkit-slider-runnable-track {
  height: 5px;
  background: #ddd;
  border: none;
  border-radius: 3px;
}

input[type='range']::-ms-track {
  height: 5px;
  background: #ddd;
  border: none;
  border-radius: 3px;
}

input[type='range']::-moz-range-track {
  height: 5px;
  background: #ddd;
  border: none;
  border-radius: 3px;
}

input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #555;
  margin-top: -5px;
  position: relative;
}

input[type='range']::-ms-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #555;
  margin-top: -5px;
  position: relative;
}

input[type='range']::-moz-range-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: #555;
  margin-top: -5px;
  position: relative;
}

input[type='range']:focus {
  outline: none;

  &::-webkit-slider-thumb:after {
    position: absolute;
    top: -35px;
    left: 50%;
    transform: translateX(-50%);
    background: #eee;
    border-radius: 5px;
    color: #555;
    padding: 5px 10px;
    border: 2px solid #555;
  }

  &::-ms-thumb:after {
    position: absolute;
    top: -35px;
    left: 50%;
    transform: translateX(-50%);
    background: #eee;
    border-radius: 5px;
    color: #555;
    padding: 5px 10px;
    border: 2px solid #555;
  }

  &::-moz-range-thumb:after {
    position: absolute;
    top: -35px;
    left: 50%;
    transform: translateX(-50%);
    background: #eee;
    border-radius: 5px;
    color: #555;
    padding: 5px 10px;
    border: 2px solid #555;
  }
}

input[type='range']:focus::-webkit-slider-runnable-track {
  background: #ccc;
}

input[type='range']:focus::-ms-track {
  background: #ccc;
}

input[type='range']:focus::-moz-range-track {
  background: #ccc;
}

.container .menu {
  position: relative;
  padding-bottom: 0;
  margin-top: 10px;
}

.hamburger {
  width: 30px;
  position: relative;
  cursor: pointer;
  height: 20px;
  margin-top: 3px;

  .a, .b, .c {
    background: #484848;
    width: 30px;
    height: 4px;
    position: absolute;
    border-radius: 2px;
    transition: all 0.5s;
    transition-delay: 0.5s;
  }

  .a {
    transform: translateY(0px);
  }

  .b {
    // transition: opacity 0.3s;
    transform: translateY(8px);
  }

  .c {
    transform: translateY(16px);
  }

  &.arrow {
    .b {
      opacity: 0;
    }

    .a {
      width: 25px;
      transform: translate(-3px, 8px) rotate(-45deg);
    }

    .c {
      width: 25px;
      transform: translate(13px, 8px) rotate(45deg);
    }
  }
}

.searchIcon {
  width: 30px;
  right: 0;
  position: absolute;
  top: 0;

  path {
    fill: #484848;
  }
}

.saveIcon {
  cursor: pointer;
  width: 25px;
  left: calc(50% - 12px);
  position: absolute;
  top: 0;

  path {
    fill: #484848;
  }
}
</style>
